<template>
	<div class="home">
		<v-row>
			<v-btn style="margin-top: 25px; margin-left: 50px" color="primary" @click="srt = !srt">Sort By price</v-btn>
			<v-col col="8" sm="4">
				<v-text-field
        flat
        hide-details
        prepend-inner-icon="mdi-magnify"
        label="Search"
				@change="filterItems"
        v-model="search"/>
      </v-col>
      <v-slider
        v-model="slider"
        class="align-center mr-6"
				label="Max Price"
        :max="100"
        :min="0"
        hide-details>
        <template v-slot:append>
          <v-text-field
            v-model="slider"
            class="mt-0 pt-0"
            hide-details
            single-line
            type="number"
            style="width: 60px"
          ></v-text-field>
        </template>
      </v-slider>

		</v-row>
		<div id="items">
			<v-row>
				<v-col
				cols="3" 
				sm="3" 
				md="4"
				v-for="(row, idx) in items"
				v-bind:key="items[idx][0]"
				>
					<v-card
					class="mx-auto my-12"
					max-width="374"
					>
						<template slot="progress">
							<v-progress-linear
							color="deep-purple"
							height="10"
							indeterminate
							></v-progress-linear>
						</template>

						<v-img
						height="250"
						aspect-ratio="1.7"
						contain
						:src="items[idx][4]"
						></v-img>

						<v-card-title>{{items[idx][1]}}</v-card-title>

						<v-card-text>
						<div class="my-4 subtitle-1">
							$ {{items[idx][2]}}
						</div>

						<div>{{items[idx][3]}}</div>
						</v-card-text>
						<v-card-actions>
							<v-btn
							color="deep-purple lighten-2"
							text
							:to="{name: 'item',params: {id: items[idx][0]}}"
							>
								View an item
							</v-btn>
						</v-card-actions>
					</v-card>
				</v-col>
			</v-row>
		</div>
	</div>
</template>

<script>
	// import Vue from 'vue'
	// connecting to the firebase database
	import * as firebase from "firebase/app"
	require('firebase/auth')
	require('firebase/database')
	export default {
		name: "home",
		components: {},
		data() {
			return {
				search: "",
        slider: 100,
				srt: false,
				items: [ 
					// {
					// 	id: 2,
					// 	name: "Example",
					// 	price: 123,
					// 	description: "Description of the product",
					// 	src: '../assets/logo.svg'
					// }
				], // 2 dimensional array with filtered items
				allItems: [] // 2 dimensional array with all items
			}
		},
		methods: {
			pushItems(snap) { // load items from the database
				for (let i = 1; i < snap.length; i++) {
					this.allItems.push([snap[i].id, snap[i].name, snap[i].price, snap[i].description, snap[i].src]);
				}
				this.items = this.allItems;
				this.sortItems();
				this.allItems = this.items;
			},
			// view(ID) {
			// 	this.$router.replace({ path: `/item/${ID}`, params: { id: ID } })
			// },
			sort(l, r) { // recusive implementation of merge sort to sort items by their price
				if (l > r) { // if the range is empty
					return [];
				}
				let tmp = [], mid = Math.floor((l + r) / 2); // get the middle range
				// console.log(l, r, mid);
				if (l == r) {
					tmp.push(this.items[l]);
					return tmp; // if the recusrsion has ended
				}
				let ls = this.sort(l, mid);
				let rs = this.sort(mid + 1, r); // sort left and right parts
				let lsz = ls.length, rsz = rs.length, i = 0, j = 0;
				while (i < lsz || j < rsz) { // merging left and right parts together 
					if (i == lsz) {
						tmp.push(rs[j++]);
					}
					else if (j == rsz) {
						tmp.push(ls[i++]);
					}
					else {
						if (ls[i][2] < rs[j][2] || (ls[i][2] == rs[j][2] && ls[i][0] < rs[j][0])) { // comparing prices and IDs in case of equal prices
							tmp.push(ls[i++]);
						}
						else {
							tmp.push(rs[j++]);
						}
					}
				}
				return tmp; // return the sorted range
			},
			sortItems() { 
				this.items = this.sort(0, this.items.length - 1); // start the recursion with the set range
			},
			filterItems() {
				let res = [];
				this.items = this.allItems;
				this.sortItems();
				for (let i = 0; i < this.items.length; i++) { // search items that contain given string
					if (this.search === "" || this.items[i][1].toLowerCase().includes(this.search.toLowerCase())) { // convert the name of items to the lower case so they can be comparable
						res.push(this.items[i]);
					}
				}

        let L = 0, R = res.length;
        while (L < R) { // binary search
          let mid = Math.floor((L + R) / 2);
					if (res[mid][2] > this.slider) { // comparing prices
						R = mid;
					}
					else {
						L = mid + 1;
					}
        }
				res = res.slice(0, L); // remove unwanted items
				if (this.srt) {
					res.reverse();
				}
				this.items = res;
			}
		},
		computed: {
		
		},
		watch: { // update the list of items if filtering options change
			search: function() {
				this.filterItems();
			},
			srt: function() {
				this.filterItems();
			},
			slider: function() {
				this.filterItems();
			}
		},
		created() {
			// localStorage.clear();
			firebase.default.database().ref().child("items")
			.get().then((snap) => { // init all items
				if (snap.exists()) {
					this.pushItems(snap.val());
				} 
				else {
					console.log("No items available");
				}
			}).catch((error) => {
				console.error(error);
			});
		}
	};
</script>
<style >
	div {
		color: inherit;
	}
</style>