<template>
	<v-app>
		<v-row justify="center">
			<v-col cols="6">
				<v-container class="justify-center">
					<h3>Total Price: ${{sum}}</h3>
					<v-data-table
						:headers="headers"
						:items="items"
						v-model="selected"
						item-key="iid"
						show-select
						class="elevation-1 mt-3"
						:hide-default-footer="true">
					</v-data-table>
					<v-btn class="mt-6" color="primary" @click="deleteItem">Delete</v-btn>
				</v-container>
			</v-col>
		</v-row>
	</v-app>
</template>



<script>
	// import Vue from 'vue'
	// import * as firebase from "firebase/app"
	require('firebase/auth')
	require('firebase/database')
	export default {
		name: "card",
		components: {},
		data() {
			return {
				items: [], // cart content
				selected: [],
				headers: [ // table attributes
          { text: 'Name', value: 'name'},
          { text: 'Price', value: 'price' } 
        ]
			}
		},
		computed: {
			sum() { // calculated total sum of the cart items
				let res = 0;
				this.items.forEach(item => res += item.price);
				return res;
			}
		},
		methods: {
			deleteItem () { // deleting process
        if(this.selected.length > 0 && 
        confirm('Are you sure you want to delete this item?')) { // confirm the deletion
          for (let i = 0; i < this.selected.length; i++) { // go through the selected items and delete them one by one
            const index = this.items.indexOf(this.selected[i]);
						for (let j = index + 1; j < this.items.length; j++) { // shift right items to the left by one index
							this.items[j - 1] = this.items[j];
						}
            this.items.pop(); // reduce the cart size
          }

          // refresh the cart content in the localStorage
          this.selected = [];
          localStorage.setItem("cart", JSON.stringify(this.items));
          var cart = [];
          if (localStorage.getItem('cart') !== null) {
            var get = JSON.parse(localStorage.getItem("cart") || "[]");
            cart = get;
          }
          this.items = cart;
        }
      }
		},
		created() {
			// localStorage.clear();
      // load the localStorage content
			let cart = [];
			if (localStorage.getItem('cart') !== null) {
				let get = JSON.parse(localStorage.getItem("cart") || "[]");
				cart = get;
			}
			this.items = cart;
		}
	}
</script>

<style scoped>
	#app {
		margin: 100px auto;
	}
</style>