<template>
	<v-app>
		<v-row justify="center">
			<v-col cols="8">
				<v-container class="justify-center">
					<v-card justify="center" class="mx-auto" cols="8">
						<v-img
							class="white--text align-end"
							height="100%"
							:src="item.src"
							align="stretch"
						>
							<v-card-title id="title">{{item.name}}</v-card-title>
						</v-img>
						<v-card-subtitle class="pb-0">
							${{item.price}}
						</v-card-subtitle>
						<v-card-text class="text--primary">
							<div>{{item.description}}</div>
						</v-card-text>
						<v-card-actions>
							<v-btn color="orange" text @click="addToCard">
								Add to cart
							</v-btn>
						</v-card-actions>
					</v-card>
				</v-container>
			</v-col>
		</v-row>
	</v-app>
</template>
<script>
	// import Vue from 'vue'
	import * as firebase from "firebase/app"
	require('firebase/auth')
	require('firebase/database')
	export default {
		name: "item",
		components: {},
		data() {
			return {
				id: 0,
				item: {

				}
			}
		},
		methods: {
			addToCard() { // adding new item to the card
				var cart = [];
				if (localStorage.getItem('cart') !== null) {
					var get = JSON.parse(localStorage.getItem("cart") || "[]");
					cart = get;
				}
				let itemCopy = this.item;
				itemCopy.iid = Math.random();
				console.log(itemCopy, this.item);
				cart.push(itemCopy);
				localStorage.setItem("cart", JSON.stringify(cart)); // update current cart
			}
		},
		created() {
			this.id = this.$route.params.id;
			firebase.default.database().ref().child("items/" + this.id).get().then((snap) => {
				if (snap.exists()) {
					this.item = snap.val();
					// console.log(snap.val());
				}
        else {
					console.log("No data available");
				}
			}).catch((error) => {
				console.error(error);
			});
		}
	};
</script>

<style scoped>
	#title {
		-webkit-text-fill-color: black; 
		-webkit-text-stroke-width: 0.2px;
		-webkit-text-stroke-color: white;
	}
</style>